body {
  background-color: #fff;
}

.header {
  height: 70px;
  background-color: #b0c4de;
}

.container {
  padding-top: 20px;
  padding-bottom: 20px;
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-input {
  width: 300px;
  height: 40px;
  border: 1px solid #fff;
  border-radius: 4px;
  margin-top: 12px;
}

.search-btn {
  height: 40px;
  width: 80px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-top: 12px;
  margin-left: 10px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.search-btn:hover {
  color: #f0f8ff;
  background-color: #7b68ee;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: flex;
}

.thumb {
  height: 500px;
  width: 600px;
  border-radius: 10px;
  overflow: hidden;
}

.thumb > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.photo-card {
  width: 600px;
  background-color: #b0c4de;
  border-radius: 10px;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  width: 300px;
  height: 50px;
  background-color: #b0c4de;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  font-size: 24px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.load-more:hover {
  color: #f0f8ff;
  background-color: #7b68ee;
}

.is-hidden {
  display: none;
}

